/**=====================
     38. Landing CSS Start
==========================**/
$landing-color : var(--primary-color);
html[landing-attr="landing-page"] {
  &[dir="rtl"] {
    direction: ltr;
  }
}
body[landing-attr="landing-page"] {
  background-color: transparent !important;
  color: unset !important;
  &[main-theme-layout="rtl"] {
    direction: ltr;
  }
  &[class="box-layout"] {
    .page-wrapper {
      .page-body-wrapper {
        width: 100%;
      }
    }
  }
}
@media screen and (min-width: 1440px){
  .landing-page{
    .container {
      max-width: 1420px;
    }
  }
}
.navbar-nav{
  align-items: center;
  @media only screen and (max-width: 1199px){
    padding-bottom: 15px;
  }
  .nav-item {
    position: relative;
    .nav-link {
      color: $black;
      font-size: 15px;
      font-weight: 500;
      letter-spacing: 1px;
      padding: 12px;
      transition: all 0.3s ease;
      opacity: 0.7;
      &:hover,&.active {
        opacity: 1;
        text-shadow: 0 0 0 var(--primary-color);
        transition: all 0.3s ease;
        color: var(--primary-color);
      }
    }
  }
}
.bg-sky-blue {
  .sticky-header{
    header{
      background: $white;
      .navbar-toggler{
        span{
          background-color: var(--primary-color);
        }
      }
    }
  }
}
.landing-main {
  .landing-home {
    background-position: right bottom;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
  }
  .navbar_nav_modify{
    .btn-light{
      &:hover{
        background-color: $white !important;
        border-color: $white !important;
        animation : 0.3s pulse 3;
      }
    }
  }
  .landing-left {
    height: 100%;
    display: flex;
    align-items: center;
    h5 {
      margin-bottom: 20px;
      line-height: 1.5;
    }
    h1 {
      font-weight: 900;
      margin-bottom: 30px;
    }
    p {
      font-size: 15px;
      margin-bottom: 25px;
    }
    .button-bottom {
      .btn-outline-primary-2x {
        background-color: $white;
      }
    }
  }
  .landing-body {
    height: calc(100vh - 100px);
    display: flex;
    align-items: center;
  }
}
.live-box {
  padding: 8px;
  background-color: $white;
  box-shadow: 0 0.5rem 1rem rgba($black, 0.15);
  position: relative;
  &:hover {
    &:before {
      opacity: 1;
      transform: scale(1);
      transition: all 0.3s ease;
    }
    .btn {
      opacity: 1;
      transition: all 0.3s ease;
    }
  }
  &:before {
    opacity: 0;
    content: "";
    position: absolute;
    background-color: rgba($black, 0.2);
    z-index: 2;
    top: 0;
    width: 100%;
    height: 100%;
    left: 0;
    transform: scale(0);
    transition: all 0.3s ease;
  }
  .btn {
    position: absolute;
    top: 45%;
    left: 30%;
    opacity: 0;
    transition: all 0.3s ease;
    z-index: 3;
  }
}
.gray-section-bg {
  background-color: $light;
}
.navbar-expand-xl {
  .navbar-brand {
    padding-left: 18px;
  }
  .navbar-collapse {
    flex-flow: row nowrap;
    justify-content: flex-end;
    background-color: $white;
  }
}
.section-space {
  padding: 80px 0;
  text-align: center;
  p {
    font-size: 15px;
  }
}
.landing-header {
  border-bottom: 1px solid $white;
  height: 100px;
  .m-tb-10 {
    margin: 21px 0;
  }
  .nav-set{
    margin: 0 110px;
  }
  h2 {
    text-transform: capitalize;
    margin-bottom: 20px;
    font-weight: 700;
    color: var(--dark-card-background);
  }
  p {
    color: var(--dark-gray);
    margin: 0 auto;
    padding-bottom: 20px;
  }
}
.admin-options {
  .live-demo-btn {
    margin-top: 30px;
    margin-bottom: 70px;
    a {
      text-transform: uppercase;
      color: $white;
      &:focus {
        box-shadow: none;
      }
    }
  }
}
.updates-features {
  .row {
    justify-content: center;
  }
  h6 {
    margin-top: 30px;
    text-transform: uppercase;
    font-weight: 700;
  }
}
.advance-bg {
  // background-image: url(../../images/landing/background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  display: flex;
  align-items: center;
  background-position: center;
}
.application-section {
  .nav-pills {
    justify-content: center;
    a {
      color: $black;
      &.active {
        color: $white;
        background-color: $info-color;
        &:before {
          width: 0;
          height: 0;
          border-left: 7px solid transparent;
          border-right: 7px solid transparent;
          border-top: 7px solid $info-color;
          content: "";
          bottom: -7px;
          position: absolute;
          left: 0;
          right: 0;
          margin: 0 auto;
          z-index: 2;
        }
        &:after {
          width: 0;
          height: 0;
          border-left: 7px solid transparent;
          border-right: 7px solid transparent;
          border-top: 7px solid $info-color;
          content: "";
          bottom: -7px;
          position: absolute;
          margin: 0 auto;
          left: 0;
          right: 0;
          z-index: 1;
        }
      }
    }
    .application-icons {
      i {
        font-size: 24px;
        margin-right: 0;
      }
    }
  }
  .tab-content {
    img {
      box-shadow: 0 0.5rem 1rem rgba($black, 0.15);
      border: 1px solid $white;
    }
    &.active {
      -webkit-animation: fadeInDown 0.3s linear;
      animation: fadeInDown 0.3s linear;
    }
  }
}
.advance-widget {
  background-color: var(--auth-bg-color);
  .widgets-section {
    // background-image: url(../../images/landing/widgets-bck.png);
    background-size: cover;
    height: 600px;
    animation: widget-animation 110s linear infinite reverse;
    -webkit-animation: widget-animation 110s linear infinite reverse;
    background-repeat: repeat-x;
  }
  @keyframes widget-animation {
    0% {
      background-position-x: 0;
    }
    100% {
      background-position-x: 1920px;
    }
  }
}
.key-features-section {
  .feature-main {
    padding: 0 25px 25px 25px;
    .feature-icon{
      width: 50px;
      margin: 0 auto;
    }
    .feature-content {
      margin-top: 35px;
      h5 {
        font-weight: 600;
        margin-bottom: 15px;
      }
      p {
        color: var(--dark-gray);
      }
    }
  }
}
.many-feature-section {
  h5 {
    text-align: left;
    margin-bottom: 20px;
    span {
      margin-left: 15px;
    }
  }
}
.exclusive-features {
  .container{
    .row {
      &:last-child {
        margin-bottom: 0;
      }
    }
    >.row{
      &:nth-child(odd){
        .inner-block {
          background-image: linear-gradient(to right, rgba(255, 0, 0, 0), $white);
        }
      }
      .inner-block {
        height: calc(100% - 30px);
        background-color: $white;
        display: flex;
        align-items: center;
        justify-content: center;
        background-image: linear-gradient(to left, rgba(255, 0, 0, 0), $white);
        padding: 50px;
        margin-bottom: 50px;
      }
    }
  }
  // background-image: url("../../images/landing/home/pattern.jpg");
  background-blend-mode: overlay;
  background-color: rgba($white, 0.95);
  background-position:0;
}
