      position: absolute;
      left: 90px;
      margin: 0 auto;
      top: 15px;
    }
    .vl:before{
      content: '';
      border-left: 1px solid $white;
      height: 35px;
      position: absolute;
      right: 90px;
      margin: 0 auto;
      top: 15px;
    }
    .list-inline-item{
      margin:0  25px;
