}
.landing-animation{
  .circle-1{
    width: 30px;
    height: 30px;
    background: #fcc604;
    border-radius: 50%;
    left: -15px;
    position: absolute;
    animation: animationFramesOne 50s infinite linear;
  }
  .circle-2{
    width: 10px;
    height: 10px;
    background: #3db310;
    border-radius: 50%;
    left: 8%;
    bottom: 15%;
    position: absolute;
    animation: animationFramesOne 40s infinite linear;
  }
  .cross-1{
    background: red;
    height: 15px;
    position: relative;
    left: -17%;
    width: 2px;
    bottom: -40%;
    animation: rotated 20s infinite linear;
    &:after{
      background: red;
      content: "";
      height: 2px;
      left: -6px;
      position: absolute;
      top: 7px;
      width: 15px;
      animation: rotated 20s infinite linear;
    }
  }
  .cross-2{
    background: #fcc604;
    height: 15px;
    position: relative;
    left: 10%;
    width: 2px;
    top: 10%;
    animation: rotated 20s infinite linear;
    &:after{
      background: #fcc604;
      content: "";
      height: 2px;
      left: -6px;
      position: absolute;
      top: 7px;
      width: 15px;
      animation: rotated 20s infinite linear;
    }
  }
  .bg-circle-1{
    width: 20px;
    height: 20px;
    border: 3px solid #dfdcf9;
    border-radius: 50%;
    position: absolute;
    top: 15%;
    left: 25%;
    animation: animationFramesOne 15s infinite linear;
  }
  .bg-circle-2{
    width: 15px;
    height: 15px;
    border: 3px solid #dfdcf9;
    border-radius: 50%;
    position: absolute;
    bottom: 15%;
    left: 15%;
    animation: animationFramesOne 40s infinite linear;
  }
  .star-animate{
    position:absolute;
    bottom: 10%;
    left: 25%;
    animation: animationFramesOne 70s infinite linear;
  }
  .star-animate-1{
    position:absolute;
    top: 10%;
    left: 15%;
    height:20px;
    width:20px;
    animation: animationFramesOne 80s infinite linear;
  }
}
}
}
