    height: 40px;
    filter: grayscale(1);
    transition: all 0.3s ease;
  }
}
.btn-md {
  background-color: $white;
  text-transform: uppercase;
  font-size: 14px;
  width: auto;
  transition: .5s ease-in-out;
  height: 40px;
  letter-spacing: 1px;
  line-height: 2;
  &:hover{
    animation: 0.3s pulse 3;
  }
}
