  .landing_first_section_img {
    position: relative;
    display: flex;
    left: 0;
    right: 0;
    margin: 20px auto 0;
    align-items: center;
    text-align: center;
    width: 100%;
  }
}
@keyframes move-img {
  0% {
    background-position: 0;
  }
  100% {
    background-position: 100%;
  }
}
@keyframes move-img1 {
  0% {
    background-position: 100%;
  }
  50% {
    background-position: 50%;
  }
  100% {
    background-position: 0;
  }
}
@keyframes move-img_before {
  0% {
    background-position: 0;
  }
  100% {
    background-position: 100%;
  }
}
.landing_gallery_section_img{
  display: flex;
  align-items: flex-end;
  text-align: end;
  left: 0;
  bottom: 0;
  &:before{
    content: '';
    height: 500px;
    bottom: -40;
    background: linear-gradient(to top,$dark-blue 8%,transparent 92%);
    z-index: 1;
    left: 0;
    position: absolute;
    width: 100%;
  }
  .img-gallery-set1, .img-gallery-set2, .img-gallery-set3, .img-gallery-set4, .img-gallery-set5{
    margin: 0 6px;
    bottom: 20px;
  }
}
.triangle_set{
  position: absolute;
  right: 25%;
  top: 17%;
  .triangle{
    position: relative;
    width: 0;
    border-bottom: solid 24px $white;
    border-right: solid 4px transparent;
    border-left: solid 4px transparent;
    animation: rotated 20s infinite linear;
    .empty{
      position: absolute;
      top: 7px;
      left: -4px;
      width: 0;
      border-bottom: solid 16px $landing-color;
      border-right: solid 4px transparent;
      border-left: solid 4px transparent;
    }
  }
}
@keyframes animationFramesTwo{
  0% {
    transform: translate(0px,0px) rotate(0deg) scale(1);
  }
  20% {
    transform: translate(73px,-1px) rotate(36deg) scale(0.9);
  }
  40% {
    transform: translate(141px,72px) rotate(72deg) scale(1);
  }
  60% {
    transform: translate(83px,122px) rotate(108deg) scale(1.2);
  }
  80% {
    transform: translate(-40px,72px) rotate(144deg) scale(1.1);
  }
  100% {
    transform: translate(0px,0px) rotate(0deg) scale(1);
  }
}
@keyframes animationFramesOne {
  0% {
    transform: translate(0px, 0px) rotate(0deg);
  }
  20% {
    transform: translate(73px, -1px) rotate(36deg);
  }
  40% {
    transform: translate(141px, 72px) rotate(72deg);
  }
  60% {
    transform: translate(83px, 122px) rotate(108deg);
  }
  80% {
    transform: translate(-40px, 72px) rotate(144deg);
  }
  100% {
    transform: translate(0px, 0px) rotate(0deg);
  }
}
